import React, {Component} from 'react';
import {Link} from "react-router-dom"
import { connect } from "react-redux";
import PropTypes from "prop-types";

class Landing extends Component {

    componentDidMount(){
        if(this.props.security.validToken)
        {
            this.setState({
                isLogEnabled: true
            });


            this.props.history.push("/");
        }
    }

    render() {

        let isLogged;
        if(this.props.security.validToken)
        {
            isLogged = (
                <div>
                    <Link className="btn btn-lg btn-primary mr-2 disabled" to="/register">
                        Sign Up
                    </Link>
                    <Link to="/login" className="btn btn-lg btn-secondary mr-2 disabled" >
                        Login
                    </Link>
                </div>
            );
        }
        else
        {
            isLogged = (
                <div>
                    <Link className="btn btn-lg btn-primary mr-2" to="/register">
                        Sign Up
                    </Link>
                    <Link to="/login" className="btn btn-lg btn-secondary mr-2">
                        Login
                    </Link>
                </div>
            );
        }

        return (
            <div className="landing">
                <div className="light-overlay landing-inner text-dark">
                    <div className="container">
                        <div className="row">
                            <div className="col-md-12 text-center">
                                <h1 className="display-3 mb-4">Personal Project Tool</h1>
                                <p className="lead">
                                    Create your account to join active projects or start you own
                                </p>
                                <hr/>
                                {isLogged}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

Landing.proptype = {
    security: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
    security: state.security
});

export default connect(mapStateToProps) (Landing);


