import axios from "axios"
import {GET_ERRORS, SET_CURRENT_USER} from "./types";
import setJWTToken from "../securityUtils/setJWTToken";
import jwt_decode from "jwt-decode";

const baseURL = "https://ppotool.herokuapp.com";

export const createNewUser = (newUser, history) => async dispatch => {
    try {
        await axios.post(baseURL + "/api/users/register", newUser);
        history.push("/login");
        dispatch ({
            type: GET_ERRORS,
            payload: {}
        });
    }
    catch (error) {
        dispatch ({
            type: GET_ERRORS,
            payload: error.response.data
        });
    }
};

export const login = LoginRequest => async dispatch => {

    try {
        // post => Login Request
        const res = await axios.post(baseURL + "/api/users/login", LoginRequest);

        // extract token from res.data
        const {token} = res.data;

        // store the token in the localStorage
        localStorage.setItem("jwtToken", token);

        // set out token in header ***
        setJWTToken(token);

        // decode token on React
        const decoded = jwt_decode(token);

        // dispatch to our securityRender
        dispatch({
            type: SET_CURRENT_USER,
            payload: decoded
        });
    }
    catch (error) {
        dispatch({
            type: GET_ERRORS,
            payload: error.response.data
        })
    }
};

export const logout = () => dispatch => {

    localStorage.removeItem("jwtToken");

    setJWTToken(false);
    dispatch({
        type: SET_CURRENT_USER,
        payload: {}
    });
};


