import React, {Component} from 'react';
import {Link} from "react-router-dom";
import {getTasks} from "../../actions/backlogActions";
import PropTypes from "prop-types";
import {connect} from "react-redux";
import ProjectTask from "../ProjectBoard/ProjectTasks/ProjectTask";

class ProjectBoard extends Component {

    constructor(props)
    {
        super(props);
        this.state = {
            params: this.props.match.params
        };

        this.props.getTasks(this.state.params.id);
    }

    componentDidMount(){
        //this.props.getTasks(this.state.params.id);
    };

    render() {

        const {id} = this.props.match.params;
        const {backlog} = this.props;

        let todo = [];
        let done = [];
        let inProgress = [];

        if(backlog.project_tasks.length !== 0)
        {
            todo = backlog.project_tasks.filter(element => element.status === 'TO_DO');
            done = backlog.project_tasks.filter(element => element.status === 'DONE');
            inProgress = backlog.project_tasks.filter(element => element.status === 'IN_PROGRESS');
        }


        return (
            <div className="container">
                <Link to={`/addProjectTask/${id}`} className="btn btn-primary mb-3">
                    <i className="fas fa-plus-circle"> Create Project Task</i>
                </Link>
                <br/>
                <hr/>
                {/*Backlog STARTS HERE*/}
                <div className="container">
                    <div className="row">
                        <div className="col-md-4">
                            <div className="card text-center mb-2">
                                <div className="card-header bg-secondary text-white">
                                    <h3>TO DO</h3>
                                </div>
                            </div>

                            {/*SAMPLE PROJECT TASK STARTS HERE */}
                            {
                               todo.map(task => (
                                   <ProjectTask key={task.id} task = {task}/>))
                            }
                            {/*SAMPLE PROJECT TASK ENDS HERE*/}
                        </div>
                        <div className="col-md-4">
                            <div className="card text-center mb-2">
                                <div className="card-header bg-primary text-white">
                                    <h3>In Progress</h3>
                                </div>
                            </div>
                            {
                                inProgress.map(task => (<ProjectTask key={task.id} task = {task}/>))
                            }
                        </div>
                        <div className="col-md-4">
                            <div className="card text-center mb-2">
                                <div className="card-header bg-success text-white">
                                    <h3>Done</h3>
                                </div>
                            </div>
                            {
                                done.map(task => (<ProjectTask key={task.id} task = {task}/>))
                            }
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

ProjectBoard.propTypes = {
    backlog:PropTypes.object.isRequired,
    getTasks:PropTypes.func.isRequired
};

const mapStateToProps = state => ({
     backlog:state.backlog
});

export default connect(mapStateToProps, {getTasks})(ProjectBoard);


