import axios from "axios";
import {DELETE_PROJECT_TASK, GET_BACKLOG, GET_ERRORS, GET_PROJECT_TASK} from "./types";

const baseURL = "https://ppotool.herokuapp.com";

export const addProjectTask = (backlog_id, project_task, histroy) => async dispatch =>
{
    await axios.post(baseURL.concat(`/api/backlog/${backlog_id}`), project_task);
    histroy.push(`/projectBoard/${backlog_id}`);

};

export const getTasks = (backlog_id) => async dispatch => {
    const res = await axios.get(baseURL.concat(`/api/backlog/${backlog_id}`));
    dispatch({
        type: GET_BACKLOG,
        payload: res.data
    })
};

export const getTask = (backlog_id, sequence_id, history) => async dispatch => {
   try {
       const res = await axios.get(baseURL.concat(`/api/backlog/${backlog_id}/${sequence_id}`));
       dispatch({
           type: GET_PROJECT_TASK,
           payload: res.data
       })
   }
   catch (error) {
       history.push("/dashboard");
   }
};

export const updateProjectTask = (backlog_id, sequence_id, project_task, history
) => async dispatch => {
    try {
        await axios.patch(baseURL.concat(`/api/backlog/${backlog_id}/${sequence_id}`), project_task);
        history.push(`/projectBoard/${backlog_id}`);
        dispatch({
            type: GET_ERRORS,
            payload: {}
        });
    } catch (err) {
        dispatch({
            type: GET_ERRORS,
            payload: err.response.data
        });
    }
};

export const deleteTask = (backlog_id, sequence_id) => async dispatch => {

    if(window.confirm("Are you shure? This will delete the Task!"))
    {
        await axios.delete(baseURL + `/api/backlog/${backlog_id}/${sequence_id}`);
        dispatch({
            type: DELETE_PROJECT_TASK,
            payload: sequence_id
        })
    }
};


