import axios from "axios";
import {GET_ERRORS, GET_PROJECT, GET_PROJECTS, DELETE_PROJECT} from "./types";

const baseURL = "https://ppotool.herokuapp.com";

export const createProject = (project, history) => async dispatch => {
  try{
      await axios.post(baseURL + "/api/project", project);
      history.push("/dashboard");
      dispatch({
          type:GET_ERRORS,
          payload:{}
      });
  }
  catch (err) {
        dispatch({
            type:GET_ERRORS,
            payload:err.response.data
        });
  }
};

export const getProjects = () => async dispatch => {
    const res = await axios.get(baseURL + "/api/project/all");
    dispatch({
        type: GET_PROJECTS,
        payload: res.data
    })
};

export const getProject = (id, history) => async dispatch => {

    try{
        const res = await axios.get(baseURL + `/api/project/${id}`);
        dispatch({
            type: GET_PROJECT,
            payload: res.data
        })
    }
    catch (error) {

        dispatch({
            type:GET_ERRORS,
            payload:error.response.data
        });

       // history.push("/dashboard");
    }
};

export const deleteProject = id => async dispatch => {

  if(window.confirm("Are you shure? This will delete the project!"))
  {
      await axios.delete(baseURL + `/api/project/${id}`);
      dispatch({
          type: DELETE_PROJECT,
          payload: id
      })
  }
};


