import React, {Component} from 'react';
import {Link} from "react-router-dom";
import {deleteTask} from "../../../actions/backlogActions";
import PropTypes from "prop-types";
import {connect} from "react-redux";

class ProjectTask extends Component{

    onDeleteClick = (backlog_id, sequence_id) => {
        this.props.deleteTask(backlog_id, sequence_id);
    };


    render()
    {
        const priority = (props) =>
        {
            return props.task.priority;
        };


        const priorityText = (props) => {

            switch (priority(props)){
                case 1:
                    return 'HIGH';
                case 2:
                    return 'MEDIUM';
                case 3:
                    return 'LOW';
                default: return null;
            }

        };

        const modifier = (props) => {

            switch (priority(props)){
                case 1:
                    return 'card-header text-light bg-danger';
                case 2:
                    return 'card-header text-light bg-warning';
                case 3:
                    return 'card-header text-light bg-info';
                default: return null;
            }
        };


        return (
            <div>
                <div className="card mb-1 bg-light">

                    <div className={modifier(this.props)}>
                        ID: {this.props.task.projectSequence} | Priority: {priorityText(this.props)}
                    </div>
                    <div className="card-body bg-light">
                        <h5 className="card-title">{this.props.task.summary}</h5>
                        <p className="card-text text-truncate ">
                            {this.props.task.acceptanceCriteria}
                        </p>
                        <Link to={`/updateProjectTask/${this.props.task.projectIdentifier}/${this.props.task.projectSequence}`}
                              className="btn btn-primary">
                            View / Update
                        </Link>

                        <button className="btn btn-danger ml-4" onClick={this.onDeleteClick.bind(this, this.props.task.projectIdentifier, this.props.task.projectSequence)}>
                            Delete
                        </button>
                    </div>
                </div>
            </div>
        );
    }

}

